/* CARD */
.event-card {
  width: 315px;
  flex-shrink: 0;
  cursor: pointer;
}

/* IMAGE */
.event-img {
  width: 100%;
  height: 120px;
  object-fit: fill;
  border-radius: 8px 8px 0 0;
}

/* TEXT */
.text-sm {
  font-size: 13px;
}

/* ELLIPSIS */
.ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

/* PAID BADGE */
.badge-paid {
  color: #1c1c1c;
  background-color: #e9d4ff;
  border: 1px solid #7f00ff;
  border-radius: 7px;
  padding: 4px 29px;
  font-size: 12px;
  font-weight: 600;
}

/* MODE */
.mode-online {
  color: #121111;
  font-weight: 700;
}

.mode-offline {
  color: #121111;
  font-weight: 700;
}

/* DATE & TIME */
.event-date {
  font-size: 12px;
  color: #1c1c1c;
}

.event-time {
  font-size: 12px;
  color: #555;
}

/* VIEW BADGE */
.view-badge {
  background: #f3f3f3;
  padding: 5px 14px;
  border-radius: 18px;
  font-size: 12px;
}
